body {
  font-family: 'Roboto';
  margin: 0;
  box-sizing: border-box;
  height: 1500px;
}

.wrapper {
  overflow: hidden;
}

/* Header */
.header {
  padding: 20px 0;
}

.logo {
  cursor: pointer;
  transition: .14s ease-in-out opacity;
  display: block;
  margin-top: 4px;
}

.logo:hover {
  opacity: .8;
}

.navigation {
  padding-top: 12px;
  text-align: right;
}

.navigation__list {
  list-style: none;
  font-size: 0;
  padding: 0;
  transition: .4s ease-in-out right;
  text-align: left;
  z-index: 2;
}

.navigation__list li {
  display: inline-block;
  margin-right: 27px;
}

.navigation__list a {
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  color: #333333;
  letter-spacing: 2.1px;
  position: relative;
}

.navigation__list a:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background-color: #366cf7;
  transition: .2s ease-in-out width;
}

.navigation__list a:hover:before {
  width: 100%;
}

.navigation__menu-toggle {
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  text-align: right;
  display: none;
  max-width: 22px;
  right: 0;
  transition: .4s ease-in-out right;
  margin-top: 2px;
}

.navigation__menu-toggle.active {
  position: absolute;
  top: 30px;
  right: 162px;
  z-index: 3;
}

.navigation__menu-toggle.active span {
  margin: 0;
}

.navigation__menu-toggle.active span:first-child {
  transform: rotate(45deg);
}

.navigation__menu-toggle.active span:nth-child(2) {
  transform: rotate(-45deg);
}

.navigation__menu-toggle.active span:last-child {
  display: none;
}

.navigation__menu-toggle span {
  display: inline-block;
  height: 2px;
  background-color: #366cf7;
  width: 22px;
  max-width: 100%;
  margin-bottom: 5px;
}

.navigation__menu-toggle span:last-child {
  margin-bottom: 0;
}

.navigation__menu-toggle:hover {
  opacity: .7;
}

.account-info {
  text-align: right;
}

.premium {
  font-size: 0;
  text-align: right;
  display: inline-block;
  vertical-align: middle;
  margin: 4px 14px 0 0;
}

.premium__title {
  font-size: 14px;
  display: inline-block;
  letter-spacing: .2px;
  vertical-align: middle;
  font-weight: bold;
  margin-right: 10px;
}

.premium__info {
  display: inline-block;
  position: relative;
  vertical-align: middle;
}

.premium__info img {
  width: 14px;
  height: 14px;
  cursor: pointer;
}

.premium__info p {
  font-size: 14px;
  position: absolute;
  background-color: white;
  top: calc(100% + 12px);
  right: -14px;
  width: 300px;
  text-align: justify;
  border: 1px solid #366cf7;
  border-radius: 6px;
  padding: 10px;
  line-height: 22px;
  visibility: hidden;
  opacity: 0;
  transition: .34s ease-in-out opacity, .34s ease-in-out visibility;
  margin: 0;
  box-shadow: 0 0 8px 0 #366cf7;
  z-index: 2;
}

.premium__info p:after, .premium__info p:before {
  content: '';
  display: block;
  position: absolute;
  left: calc(100% - 27px);
  width: 0;
  height: 0;
  border: solid 8px;
}

.premium__info p:after {
  top: -16px;
  border-color: transparent transparent white transparent;
}

.premium__info p:before {
  top: -17px;
  border-color: transparent transparent #366cf7 transparent;
}

.premium__info p span {
  color: #366cf7;
  font-weight: bold;
}

.premium__info:hover p {
  visibility: visible;
  opacity: 1;
}

.credits {
  font-size: 14px;
  line-height: 14px;
  font-weight: bold;
  color: #366cf7;
  display: inline-block;
  margin: 4px 15px 0 0;
  vertical-align: middle;
  letter-spacing: 1px;
}

.user-menu-wrap {
  display: inline-block;
  position: relative;
}

.user-menu {
  display: inline-block;
  line-height: 0;
  padding-right: 24px;
  background: url("../imgs/arrow.png") no-repeat right 55%;
  background-size: 10px;
  vertical-align: middle;
  cursor: pointer;
  transition: .3s ease-in-out background-image;
}

.user-menu.active {
  background-image: url("../imgs/close.svg");
}

.user-menu.active + .dropdown-menu {
  visibility: visible;
  opacity: 1;
}

.dropdown-menu {
  background-color: white;
  position: absolute;
  top: calc(100% + 10px);
  right: 0;
  width: 180px;
  font-size: 14px;
  text-align: left;
  list-style: none;
  margin: 0;
  padding: 10px;
  border: 1px solid #366cf7;
  border-radius: 6px;
  box-shadow: 0 0 8px 0 #366cf7;
  color: #333333;
  letter-spacing: 1px;
  line-height: 20px;
  visibility: hidden;
  opacity: 0;
  transition: .34s ease-in-out opacity, .34s ease-in-out visibility;
  word-break: break-all;
  z-index: 2;
}

.dropdown-menu a {
  display: block;
  color: #333333;
  text-decoration: none;
  transition: .14s ease-in-out opacity;
}

.dropdown-menu a:hover {
  opacity: .6;
}

.dropdown-menu span {
  font-weight: bold;
}

.dropdown-menu:after, .dropdown-menu:before {
  content: '';
  display: block;
  position: absolute;
  left: calc(100% - 40px);
  width: 0;
  height: 0;
  border: solid 8px;
}

.dropdown-menu:after {
  top: -16px;
  border-color: transparent transparent white transparent;
}

.dropdown-menu:before {
  top: -17px;
  border-color: transparent transparent #366cf7 transparent;
}

.dropdown-menu__divider {
  margin: 6px 0 7px;
  border-bottom: 1px solid #366cf7;
}

.owl-stage {
