.footer__navigation li {
  display: inline-block;
  margin-right: 45px;
  margin-bottom: 18px;
}

.footer__navigation li:last-child {
  margin-right: 0;
}

.footer__navigation a {
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  color: #777777;
  text-decoration: none;
  letter-spacing: 1px;
  opacity: 1;
  transition: .14s ease-in-out opacity;
}

.footer__navigation a:hover {
  opacity: .7;
}

.footer__copyright {
  margin-top: 4px;
}

.footer__copyright p {
  margin: 0;
  color: #777777;
  font-size: 12px;
  letter-spacing: .7px;
  line-height: 20px;
}

/* To top button */
.to-top {
  position: fixed;
  right: 45px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  text-align: center;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  background-color: #fff;
  cursor: pointer;
  transition: .14s ease-in-out background-color;
  z-index: 1;
}

.to-top:hover {
  background-color: rgba(54, 108, 247, 0.6);
}

.to-top img {
  width: 24px;
  margin-top: 11px;
}

@media screen and (max-width: 1199px) {
  .navigation {
    padding-top: 10px;
  }
  .navigation__list li {
    margin-right: 23px;
  }
  .navigation__list a {
    letter-spacing: 1px;
  }
  .premium__title {
    margin-right: 5px;
  }
  .premium,
  .credits {
    margin-top: 0;
  }
  /* Search */
  .search__city {
    max-width: 220px;
  }
  .age-range-wrap {
    width: 135px;
    margin-right: 0;
  }
  .age-title {
    margin-right: 5px;
  }
  .search__city-wrap {
    margin-right: 0;
  }
  /* Advanced search */
  .girl__photos-wrap {
    height: 210px;
  }
}

@media screen and (max-width: 991px) {
  .navigation {
    padding-top: 16px;
    order: 3;
    position: static;
  }
  .navigation__list {
    background-color: #f0f0f0;
    position: fixed;
    width: 200px;
    top: 0;
    bottom: 0;
    right: -200px;
    padding: 30px 20px;
    text-align: center;
    border-left: 1px solid #cecece;
  }
  .navigation__list.active {
    right: 0;
  }
  .navigation__list li {
    margin: 0 0 30px;
  }
  .navigation__list li:last-child {
    margin-bottom: 0;
  }
  .navigation__menu-toggle {
    display: inline-block;
  }
  .account-info {
    padding-top: 6px;
  }
  /* Advanced search */
  .girl__photos-wrap {
    height: 280px;
  }
}

@media screen and (max-width: 767px) {
  .credits,
  .premium {
    margin-right: 10px;
  }
  .account-info {
    padding-top: 0;
  }
  .navigation {
    padding: 10px 15px 0 0;
  }
  /* Search */
  .search__submit-wrap {
    text-align: right;
    margin-top: 40px;
  }
  .age-wrap {
    text-align: left;
    margin-top: 32px;
  }
  .search__gender-wrap {
    max-width: 100%;
  }
  .age-range-wrap {
    max-width: calc(100% - 73px);
    width: 100%;
  }
  /* Advanced search */
  .girl__photos-wrap {
    height: 240px;
  }
  .advanced-search__body .advanced-search__input-row label {
    padding: 4px 6px 5px;
  }
  /* To top button */
  .to-top {
    right: 20px;
  }
}

@media screen and (max-width: 575px) {
  .navigation {
    padding-top: 12px;
    order: 0;
  }
  .account-info {
    text-align: center;
    margin-top: 18px;
  }
  .premium__info p {
    left: -50px;
    right: inherit;
    width: 200px;
  }
  .premium__info p:after, .premium__info p:before {
    left: 48px;
  }
  /* Search */
  .search__header h3 {
    margin-bottom: 10px;
  }
  .search__city-wrap {
    margin-top: 46px;
  }
  .search__city {
    max-width: 100%;
  }
  .search__submit-wrap {
    text-align: center;
    margin-top: 50px;
  }
  /* Advanced search */
  .girl__photos-wrap {
    height: 270px;
  }
}
